import { Box, Card, CardActionArea, CardContent, Chip, Stack, Typography, useMediaQuery, useTheme } from '@mui/material'

import { CLUB_TYPES, type ClubType } from 'src/data/types'

import { stringToColor } from 'src/utils'

export interface ClubPreviewProps {
  id: number
  name: string
  category: string
  type: ClubType
  description: string
}

function ClubPreview({ id, name, category, type, description }: ClubPreviewProps) {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))

  return (
    <Card>
      <CardActionArea href={`/club/${id}`} sx={{ display: 'flex', flexDirection: matches ? 'column' : 'row' }}>
        <Box sx={{ backgroundColor: stringToColor(name, 0.3), height: '90px', width: { sm: '100%', xs: '10%' } }} />
        <CardContent sx={{ flex: 1, width: '100%' }}>
          <Typography gutterBottom variant="h5" component="div">
            {name}
          </Typography>
          {matches && (
            <Typography
              variant="body2"
              color="text.secondary"
              whiteSpace="nowrap"
              overflow="hidden"
              textOverflow="ellipsis"
            >
              {description}
            </Typography>
          )}
          <Stack direction="row" justifyContent="flex-start" spacing={1} sx={{ marginTop: theme.spacing(1) }}>
            {CLUB_TYPES[type] && (
              <Chip
                label={CLUB_TYPES[type].name}
                size="small"
                sx={{ padding: theme.spacing(0.5), fontSize: matches ? 13 : 11 }}
              />
            )}
            {category && (
              <Chip label={category} size="small" sx={{ padding: theme.spacing(0.5), fontSize: matches ? 13 : 11 }} />
            )}
          </Stack>
        </CardContent>
      </CardActionArea>
    </Card>
  )
}

export default ClubPreview
