import { Link } from 'react-router-dom'
import Slider from 'react-slick'

import { Box, Button, Container, type Theme, Typography, useTheme } from '@mui/material'

import ClubPreview, { type ClubPreviewProps } from 'src/components/ClubPreview'

import { useGetRecruitingClubs } from 'src/hooks/api/apply'

import 'slick-carousel/slick/slick-theme.css'
import 'slick-carousel/slick/slick.css'

interface RecruitingClubType {
  theme: Theme
  isMqMatched: boolean
}

const RecruitingClubs: React.FC<RecruitingClubType> = ({ theme, isMqMatched }) => {
  const count = isMqMatched ? 20 : 8
  const { data: clubs } = useGetRecruitingClubs(count)

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: theme.spacing(2, isMqMatched ? 0 : 2)
      }}
    >
      {clubs?.length ? (
        <Typography variant="h3" component="h2">
          현재 모집 중인 동아리예요
        </Typography>
      ) : (
        <Typography flexDirection={'row'} variant="h4" mt={2}>
          모집중인 동아리가 없어요 :(
        </Typography>
      )}
      <Container
        sx={{
          padding: theme.spacing(1, 0, 5),
          maxWidth: isMqMatched && clubs && clubs.length < 4 ? `${clubs.length * 330}px` : 'none'
        }}
      >
        {clubs && <ClubCarousel clubs={clubs} />}
      </Container>
      <Link to="search" style={{ textDecoration: 'none' }}>
        <Button variant="contained" size="large">
          전체 동아리 보기
        </Button>
      </Link>
    </Box>
  )
}

export default RecruitingClubs

interface ClubCarouselProps {
  clubs: ClubPreviewProps[]
}

const ClubCarousel: React.FC<ClubCarouselProps> = ({ clubs }) => {
  const {
    breakpoints: {
      values: { lg, sm }
    },
    spacing
  } = useTheme()

  const responsive = [
    {
      breakpoint: lg,
      settings: {
        slidesToShow: Math.min(clubs.length, 3),
        slidesToScroll: Math.min(clubs.length, 3)
      }
    },
    {
      breakpoint: sm,
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1
      }
    }
  ]

  return (
    <Slider
      dots
      infinite
      slidesToShow={Math.min(clubs.length, 4)}
      slidesToScroll={Math.min(clubs.length, 4)}
      autoplay
      responsive={responsive}
      arrows={false}
    >
      {clubs.map(club => (
        <Box key={club.id} sx={{ padding: spacing(0, 1) }}>
          <ClubPreview {...club} />
        </Box>
      ))}
    </Slider>
  )
}
