import SearchIcon from '@mui/icons-material/Search'
import { Box, FormControl, IconButton, InputAdornment, OutlinedInput, useMediaQuery, useTheme } from '@mui/material'

interface SearchboxProps {
  handleSubmit: React.FormEventHandler<HTMLFormElement>
  defaultValue: string
}
const Searchbox: React.FC<SearchboxProps> = ({ handleSubmit, defaultValue }) => {
  const theme = useTheme()
  const isUpperThanSm = useMediaQuery(theme.breakpoints.up('sm'))

  return (
    <Box component="form" onSubmit={handleSubmit} px={isUpperThanSm ? 0 : 4}>
      <FormControl variant="outlined" fullWidth sx={{ marginTop: 4, backgroundColor: 'white' }}>
        <OutlinedInput
          id="search"
          type="search"
          placeholder="동아리 이름을 검색해보세요"
          defaultValue={defaultValue}
          inputProps={{ maxLength: 50 }}
          endAdornment={
            <InputAdornment position="end">
              <IconButton aria-label="Search clubs" type="submit" edge="end">
                <SearchIcon />
              </IconButton>
            </InputAdornment>
          }
        />
      </FormControl>
    </Box>
  )
}

export default Searchbox
